import React, { Component } from 'react';
import { 
  ForwardPages,
  NextPage,
  CodeEditor
} from '../../../commonComponents'
import { code1 } from './constants'
class SetImmediate extends Component {
  componentDidMount() {
    this.props.chooseItem('30')
  }
  render() {
    return (
      <div>
        <h1 className='article-reader__headline'>setImmediate()</h1>
        <div className='articleConent'>
          <p>,setImmediate()</p>
          <CodeEditor codeContent={code1} height="80px"/>
          <p>setImmediate()</p>
          <p>setImmediatesetTimeout(0),process.nextTick?</p>
          <p>process.nextTick(),;setImmediate
            setTimeout
          </p>
          <p>0setTimeoutsetImmediate,,
            
          </p>
        </div>
        <div className="clickPage">
          <ForwardPages url="/node/NextTick" chooseItem={() => this.props.chooseItem('29')}/>
          <NextPage url="/node/Timer" chooseItem={() => this.props.chooseItem('31')}/>
        </div>
      </div>
    );
  }
}

export default SetImmediate;