import React, { Component } from 'react';
import { 
  ForwardPages,
  NextPage,
  CodeEditor
} from '../../../commonComponents'
import { code1, code2 } from './constants'
class AcceptCommand extends Component {
  componentDidMount() {
    this.props.chooseItem('13')
  }
  render() {
    return (
      <div>
        <h1 className='article-reader__headline'></h1>
        <div className='articleConent'>
          <p>node.js CLI</p>
          <p>node.js 7<a href="https://nodejs.org/api/readline.html">readline</a>:,
          process.stdin,,
          node,</p>
          <CodeEditor codeContent={code1} height="240px"/>
          <p>,enter,</p>
          <p>question()(),enter</p>
          <p>, menreadline interface</p>
          <p>readline,</p>
          <p>,,*</p>
          <p><a href="https://www.npmjs.com/package/readline-sync">readline-sync</a>,API,</p>
          <p><a href="https://github.com/SBoudrias/Inquirer.js">Inquirer.js package</a></p>
          <p>:npm install inquirer,:</p>
          <CodeEditor codeContent={code2} height="340px"/>
        </div>
        <div className="clickPage">
          <ForwardPages url="/node/OutputInCommand" chooseItem={() => this.props.chooseItem('12')}/>
          <NextPage url="/node/ExportFunctionality" chooseItem={() => this.props.chooseItem('14')}/>
        </div>
      </div>
    );
  }
}

export default AcceptCommand;