import React, { Component } from 'react';
import { 
  ForwardPages,
  NextPage,
  CodeEditor
} from '../../../commonComponents'
import {
  code1,
  code2,
  code3,
  code4,
  code5,
  code6,
  code7,
  code8,
  code9,
  code10,
  code11
} from './constants'
class OutputInCommand extends Component {
  componentDidMount() {
    this.props.chooseItem('12')
  }
  render() {
    return (
      <div>
        <h1 className='article-reader__headline'></h1>
        <div className='articleConent'>
          <h2>console</h2>
          <p>node.js<a href="https://nodejs.org/api/console.html">console</a>,
            
          </p>
          <p>console</p>
          <p>console.log(),</p>
          <p>,</p>
          <p>,:</p>
          <CodeEditor codeContent={code1} height="80px"/>
          <p>node</p>
          <p>:</p>
          <p></p>
          <CodeEditor codeContent={code2} height="40px"/>
          <ul>
            <li>%s</li>
            <li>%d</li>
            <li>%i</li>
            <li>%o</li>
          </ul>
          <p>:</p>
          <CodeEditor codeContent={code3} height="40px"/>
          <h2></h2>
          <p>console.clear()(console)</p>
          <h2></h2>
          <p>console.count()</p>
          <p>:</p>
          <CodeEditor codeContent={code4} height="400px"/>
          <p>count</p>
          <p>:</p>
          <CodeEditor codeContent={code5} height="220px"/>
          <h2>stack trace</h2>
          <p>call stack trace,</p>
          <p>console.trace()</p>
          <CodeEditor codeContent={code6} height="80px"/>
          <p>stack trace.REPL:</p>
          <CodeEditor codeContent={code7} height="290px"/>
          <h2></h2>
          <p>time()timeend():</p>
          <CodeEditor codeContent={code8} height="220px"/>
          <h2></h2>
          <p>console.log.</p>
          <p>console.error</p>
          <p>,</p>
          <h2></h2>
          <p>.</p>
          <p>:</p>
          <CodeEditor codeContent={code9} height="40px"/>
          <p>REPL,hi</p>
          <p>..<a href="https://github.com/chalk/chalk">Chalk</a>
          ,,,</p>
          <p>npmChalk,:</p>
          <CodeEditor codeContent={code10} height="80px"/>
          <h2>chalk.yellow.</h2>
          <p></p>
          <h2></h2>
          <p><a href="https://www.npmjs.com/package/progress">Progress</a>,</p>
          <p>10,100.:</p>
          <CodeEditor codeContent={code11} height="240px"/>
        </div>
        <div className="clickPage">
          <ForwardPages url="/node/commandArguments" chooseItem={() => this.props.chooseItem('11')}/>
          <NextPage url="/node/acceptCommand" chooseItem={() => this.props.chooseItem('13')}/>
        </div>
      </div>
    );
  }
}

export default OutputInCommand;