import React, { Component } from 'react';
import { 
  ForwardPages,
  NextPage,
  CodeEditor
} from '../../../commonComponents'
import codeAll from './constants'
class BuildHttpServer extends Component {
  componentDidMount() {
    this.props.chooseItem('36')
  }
  render() {
    return (
      <div>
        <h1 className='article-reader__headline'>http</h1>
        <div className='articleConent'>
          <p>hello world http webdemo:</p>
          <CodeEditor codeContent={codeAll.code1} height="360px"/>
          <p>:</p>
          <CodeEditor codeContent={codeAll.code2} height="40px"/>
          <p>,<a href="https://nodejs.org/api/http.html">http module</a></p>
          <p>http</p>
          <p>:3000.,</p>
          <p>request.request,
            <a href="https://nodejs.org/api/http.html#http_event_request">request</a>
            ,:request(<a href="https://nodejs.org/api/http.html#http_class_http_incomingmessage">http.IncomingMessage</a>)
            response(<a href="https://nodejs.org/api/http.html#http_class_http_serverresponse">http.ServerResponse</a>)
          </p>
          <p>requestrequest.,</p>
          <p>response</p>
          <p>:</p>
          <CodeEditor codeContent={codeAll.code3} height="40px"/>
          <p>statusCode200,</p>
          <p>Content-Type header:</p>
          <CodeEditor codeContent={codeAll.code4} height="40px"/>
          <p>,end()</p>
        </div>
        <div className="clickPage">
          <ForwardPages url="/node/eventEmitter" chooseItem={() => this.props.chooseItem('35')}/>
          <NextPage url="/node/httpRequest" chooseItem={() => this.props.chooseItem('37')}/>
        </div>
      </div>
    );
  }
}

export default BuildHttpServer;