import React, { Component } from 'react';
import { 
  ForwardPages,
  NextPage,
} from '../../../commonComponents'
class Dependencies extends Component {
  componentDidMount() {
    this.props.chooseItem('26')
  }
  render() {
    return (
      <div>
        <h1 className='article-reader__headline'></h1>
        <div className='articleConent'>
          <p>npm install  ,</p>
          <p>package.json,dependencies(--save)</p>
          <p>-D--save-dev,,devDependenciee</p>
          <p>,,test,webpack,Babel</p>
          <p>,npm install,npm</p>
          <p>--production(npm install --production),</p>
        </div>
        <div className="clickPage">
          <ForwardPages url="/node/GlobalOrlocally" chooseItem={() => this.props.chooseItem('25')}/>
          <NextPage url="/node/Npx" chooseItem={() => this.props.chooseItem('26')}/>
        </div>
      </div>
    );
  }
}

export default Dependencies;