import React, { Component } from 'react';
import { 
  ForwardPages,
  NextPage,
  CodeEditor
} from '../../../commonComponents'
import codeAll from './constants'
class OSModule extends Component {
  componentDidMount() {
    this.props.chooseItem('48')
  }
  render() {
    return (
      <div>
        <h1 className='article-reader__headline'>os</h1>
        <div className='articleConent'>
          <p>,,</p>
          <CodeEditor codeContent={codeAll.code1} height="40px"/>
          <p>:</p>
          <p>os.EOL.LinuxmacOS\n,Windows\r\n</p>
          <p>os.constants.signals,SIGHUP,SIGKILL</p>
          <p><a href="https://nodejs.org/api/os.html#os_signal_constants"></a></p>
          <p>os:</p>
          <h2>os.arch()</h2>
          <p>,arm,x64,arm64</p>
          <h2>os.cpus()</h2>
          <p>cpu</p>
          <p>:</p>
          <CodeEditor codeContent={codeAll.code2} height="640px"/>
          <h2>os.endianness()</h2>
          <p>BELE,nodeBig EndianLittle Endian</p>
          <h2>os.freemem()</h2>
          <p></p>
          <h2>os.homedir()</h2>
          <p>:</p>
          <CodeEditor codeContent={codeAll.code3} height="40px"/>
          <h2>os.hostname()</h2>
          <p></p>
          <h2>os.loadavg()</h2>
          <p></p>
          <p>Linux and macOS</p>
          <CodeEditor codeContent={codeAll.code4} height="40px"/>
          <h2> os.networkInterfaces()</h2>
          <p></p>
          <CodeEditor codeContent={codeAll.code5} height="940px"/>
          <h2>os.platform()</h2>
          <p>node:</p>
          <ul>
            <li>darwin</li>
            <li>freebsd</li>
            <li>linux</li>
            <li>openbsd</li>
            <li>win32</li>
            <li>...more</li>
          </ul>
          <h2>os.release()</h2>
          <p></p>
          <h2>os.tmpdir()</h2>
          <p></p>
          <h2>os.totalmem()</h2>
          <p></p>
          <h2>os.type()</h2>
          <p></p>
          <ul>
            <li>Linux</li>
            <li>macOSDarwin</li>
            <li>WindowsWindows_NT</li>
          </ul>
          <h2>os.uptime()</h2>
          <p>()</p>
          <h2>os.userInfo()</h2>
        </div>
        <div className="clickPage">
          <ForwardPages url="/node/pathModule"  chooseItem={() => this.props.chooseItem('47')}/>
          <NextPage url="/node/eventsModule"  chooseItem={() => this.props.chooseItem('49')}/>
        </div>
      </div>
    );
  }
}

export default OSModule;