import React, { Component } from 'react';
import { 
  ForwardPages,
  NextPage,
  CodeEditor
} from '../../../commonComponents'
import {
  startNode,
  nodeCommad,
  showREPL,
  logREPL,
  showArray,
  showNum
} from './constants'
import classNumber from '../../../../images/classNumber.png'
import globalObj from "../../../../images/globalObj.png"
class UseREPL extends Component {
  componentDidMount() {
    this.props.chooseItem('10')
  }
  render() {
    return (
      <div>
        <h1 className='article-reader__headline'>REPL</h1>
        <div className='articleConent'>
          <p>nodenode.js</p>
          <CodeEditor codeContent={startNode} height="40px"/>
          <p>REPL:</p>
          <CodeEditor codeContent={nodeCommad} height="40px"/>
          <p>: REPLRead Evaluate Print Loop(),,
            .
          </p>
          <p>,:</p>
          <CodeEditor codeContent={showREPL} height="60px"/>
          <p></p>
          <p>: ,</p>
          <p>REPLjs,</p>
          <p>,enter:</p>
          <CodeEditor codeContent={logREPL} height="100px"/>
          <p>test</p>
          <p>js:</p>
          <h2>tab</h2>
          <p>REPL</p>
          <p>,tab,REPL</p>
          <h2>js</h2>
          <p>js,Number,.tab</p>
          <p>REPL:</p>
          <img style={{display: 'block',width: 800,height: 350}} src={classNumber} alt=""/>
          <h2></h2>
          <p>global.,tab,:</p>
          <img style={{display: 'block',width: 800,height: 500}} src={globalObj} alt=""/>
          <h2> _</h2>
          <p>,_,</p>
          <h2>.</h2>
          <p>REPL,.:</p>
          <ul>
            <li>.help: .</li>
            <li>.editor: ,js,ctrl+d</li>
            <li>.break: ,.break.ctrl+c</li>
            <li>.clear: REPL,</li>
            <li>.load: js,</li>
            <li>.exit: REPL,ctrl+c</li>
          </ul>
          <p>.editor,REPL</p>
          <p>,:</p>
          <CodeEditor codeContent={showArray} height="40px"/>
          <p>enter,REPL...,</p>
          <CodeEditor codeContent={showNum} height="80px"/>
          <p>.break,,</p>
        </div>
        <div className="clickPage">
          <ForwardPages url="/node/readVariables" chooseItem={() => this.props.chooseItem('9')}/>
          <NextPage url="/node/commandArguments" chooseItem={() => this.props.chooseItem('11')}/>
        </div>
      </div>
    );
  }
}

export default UseREPL;