import React, { Component } from 'react';
import { 
  ForwardPages,
  NextPage,
  CodeEditor
} from '../../../commonComponents'
import codeAll from './constants'
class FsModule extends Component {
  componentDidMount() {
    this.props.chooseItem('46')
  }
  render() {
    return (
      <div>
        <h1 className='article-reader__headline'>fs</h1>
        <div className='articleConent'>
          <p>fs,,</p>
          <p>node,,require:</p>
          <CodeEditor codeContent={codeAll.code1} height="40px"/>
          <p>:</p>
          <ul>
            <li>fs.access(): ,node()</li>
            <li>fs.appendFile(): ,,</li>
            <li>fs.chmod(): .fs.lchmod,fs.fchmod</li>
            <li>fs.chown(): .fs.fchown,fs.lchown</li>
            <li>fs.close(): </li>
            <li>fs.copyFile(): </li>
            <li>fs.createReadStream(): </li>
            <li>fs.createWriteStream(): </li>
            <li>fs.link(): </li>
            <li>fs.mkdir(): </li>
            <li>fs.mkdtemp(): </li>
            <li>fs.open(): </li>
            <li>fs.readdir(): </li>
            <li>fs.readFile(): .fs.read()</li>
            <li>fs.readlink(): </li>
            <li>fs.realpath(): (. ..)</li>
            <li>fs.rename(): </li>
            <li>fs.rmdir(): </li>
            <li>fs.stat(): .fs.fstat().fs.lstat()</li>
            <li>fs.symlink(): </li>
            <li>fs.truncate(): ,,fs.ftruncate()</li>
            <li>fs.unlink(): </li>
            <li>fs.unwatchFile(): </li>
            <li>fs.utimes(): .fs.futimes()</li>
            <li>fs.watchFile(): .fs.watch()</li>
            <li>fs.writeFile(): .fa.write()</li>
          </ul>
          <p>fs,Sync:</p>
          <ul>
            <li>fs.rename()</li>
            <li>fs.renameSync()</li>
            <li>fs.write()</li>
            <li>fs.writeSync()</li>
          </ul>
          <p></p>
          <p>: node10<a href="https://nodejs.org/api/fs.html#fs_fs_promises_api"> experimental support</a>
            promise API
          </p>
          <p>fs.rename(),API:</p>
          <CodeEditor codeContent={codeAll.code2} height="160px"/>
          <p>API,try/catch:</p>
          <CodeEditor codeContent={codeAll.code3} height="200px"/>
          <p>,</p>
        </div>
        <div className="clickPage">
          <ForwardPages url="/node/folder" chooseItem={() => this.props.chooseItem('45')}/>
          <NextPage url="/node/pathModule" chooseItem={() => this.props.chooseItem('47')}/>
        </div>
      </div>
    );
  }
}

export default FsModule;