import React, { Component } from 'react';
import { 
  ForwardPages,
  NextPage,
  CodeEditor
} from '../../../commonComponents'
import { code1, code2 } from './constants'
class WherePackages extends Component {
  componentDidMount() {
    this.props.chooseItem('16')
  }
  render() {
    return (
      <div>
        <h1 className='article-reader__headline'>npm</h1>
        <div className='articleConent'>
          <p>npm:</p>
          <ul>
            <li></li>
            <li></li>
          </ul>
          <p>,:</p>
          <CodeEditor codeContent={code1} height="40px"/>
          <p>,node_modules</p>
          <p>,npmpackage.jsondependencieslodash</p>
          <p>-g:</p>
          <CodeEditor codeContent={code2} height="40px"/>
          <p>,npm,</p>
          <p>?</p>
          <p>npm-g</p>
          <p>macOSLinux/usr/local/lib/node_modules,Windows C:\Users\YOU\AppData\Roaming\npm\node_modules</p>
          <p>nvmnode,</p>
          <p>,/Users/joe/.nvm/versions/node/v8.9.0/lib/node_modules.</p>
        </div>
        <div className="clickPage">
          <ForwardPages url="/node/introductionOfNpm" chooseItem={() => this.props.chooseItem('15')}/>
          <NextPage url="/node/executePackage" chooseItem={() => this.props.chooseItem('17')}/>
        </div>
      </div>
    );
  }
}

export default WherePackages;